import DataLoader from "dataloader";
import { uniq, keyBy } from "lodash";
import { getFromLocalStorage } from "../taskpane/taskpane";
import { fmpAxiosLimiter } from "../http/limiter";

async function batchGetRatios(symbols: string[]) {
  const apiKey = getFromLocalStorage("FmpApiKey");
  const symbolsUniq = uniq(symbols);
  const results = await Promise.all(
    symbolsUniq.map(async (symbol) => {
      const url = `https://financialmodelingprep.com/api/v3/ratios-ttm/${symbol}?apikey=${apiKey}`;
      try {
        const response = await fmpAxiosLimiter.get(url);
        return response.data.length > 0
          ? {
              symbol,
              ...response.data[0],
              dividendYielTTM: response.data[0].dividendYieldTTM,
              dividendYielPercentageTTM: response.data[0].dividendYieldPercentageTTM,
            }
          : null;
      } catch (err) {
        console.error(err);
        return null;
      }
    })
  );
  const keyBySymbol = keyBy(results, "symbol");

  return symbols.map((symbol) => keyBySymbol[symbol]);
}

export default new DataLoader((symbols: string[]) => batchGetRatios(symbols));
