/**
 * Screens stocks based on given criteria.
 * @customfunction ARBOR_SCREEN
 * @param {string} query The screening criteria
 * @param {number} [direction] The direction to output the results, 0 for vertical, 1 for horizontal, default 0
 * @returns {Promise<string[][]>} An array of stock symbols that match the criteria
 */
export async function screen(query: string, direction: number = 0): Promise<string[][]> {
  try {
    const apiKey = getFromLocalStorage("GPTKey");
    if (!apiKey) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "API key not found in settings");
    }

    const client = new AzureOpenAI({
      baseURL: "https://apoidea-chatgpt-api.openai.azure.com/openai/deployments/gpt4o-mini-deployment/",
      apiVersion: "2024-05-01-preview",
      apiKey,
      deployment: "gpt4o-mini-deployment",
      dangerouslyAllowBrowser: true,
    });
    const screeningPrompt = await getFromWorkbookSettings("ScreeningPrompt");

    const response = await client.chat.completions.create({
      model: "gpt-4o-mini",
      messages: [
        {
          role: "system",
          content: screeningPrompt || defaultScreeningPrompt,
        },
        {
          role: "user",
          content: `query: ${query}`,
        },
      ],
    });

    const content = response.choices[0].message.content;
    if (!content) {
      throw new Error("No content in response");
    }

    const parsedContent = JSON.parse(content);
    const resultList: string[] = parsedContent.list || parsedContent[Object.keys(parsedContent)[0]];
    if (direction === 1) {
      return [resultList];
    }
    return resultList.map((item) => [item]);
  } catch (error) {
    console.error(`Error in SCREENING function:`, error);
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Error in SCREENING function");
  }
}

/**
 * Answers questions about a company based on its stock symbol in a short format.
 * @customfunction ARBOR_CHAT
 * @param {string} ticker The stock symbol of the company
 * @param {string} query The question to ask about the company
 * @param {boolean} [longAnswer] Whether to return a long answer
 * @returns {Promise<Excel.EntityCellValue | Excel.ErrorCellValue>} The answer to the query
 */
export async function answer(
  ticker: string,
  query: string,
  longAnswer: boolean
): Promise<Excel.EntityCellValue | Excel.ErrorCellValue> {
  try {
    const companyData = await companyProfileLoader.load(ticker);
    if (!companyData) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.notAvailable, `Record Not found: ${ticker}`);
    }
    const apiToken = getFromLocalStorage("ArborApiToken");
    const provider = await getFromWorkbookSettings("arborModelProvider");
    let queryPrompt = longAnswer ? defaultLongAnswerPrompt : defaultShortAnswerPrompt;
    queryPrompt = queryPrompt.replace(/\{\{query\}\}/g, query);
    Object.keys(companyData).forEach((key) => {
      queryPrompt = queryPrompt.replace(new RegExp(`\\{\\{${key}\\}\\}`, "g"), companyData[key]);
    });

    const requestBody = JSON.stringify({
      query: queryPrompt,
      provider: provider || "openai",
    });

    const arborAnswerApi =
      getFromLocalStorage("ArborAnswerApi") || "https://stockchat-intelligence-service.arbor.news/api/v1/answer";
    const response = await arborAxiosLimiter
      .post(arborAnswerApi, requestBody, {
        headers: {
          "Content-Type": "application/json",
          "X-API-KEY": apiToken,
          "X-API-TOKEN": apiToken,
        },
      })
      .catch((error) => {
        console.error(`Error in ANSWER function:`, error);
        throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, error.message);
      });

    if (response.status >= 400) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const data = response.data;

    if (data.success && data.result?.answer) {
      const entity: Excel.EntityCellValue = {
        type: Excel.CellValueType.entity,
        text: data.result.answer,
        properties: {
          type: {
            type: Excel.CellValueType.string,
            basicValue: longAnswer ? "long" : "short",
          },
          success: {
            type: Excel.CellValueType.boolean,
            basicValue: true,
          },
          ticker: {
            type: Excel.CellValueType.string,
            basicValue: ticker,
          },
          query: {
            type: Excel.CellValueType.string,
            basicValue: query || "",
          },
          answer: {
            type: Excel.CellValueType.string,
            basicValue: data.result.answer || "",
          },
          answerAt: {
            type: Excel.CellValueType.string,
            basicValue: new Date(data.result.answerAt).toISOString() || "",
          },
          answerId: {
            type: Excel.CellValueType.string,
            basicValue: data.result.id || "",
          },
          queryAt: {
            type: Excel.CellValueType.string,
            basicValue: new Date(data.result.queryAt).toISOString() || "",
          },
          references: {
            type: Excel.CellValueType.string,
            basicValue: data.result.references.map((ref) => ref.url).join("\n"),
          },
        },
        basicType: Excel.RangeValueType.error,
        basicValue: "#VALUE!",
      };
      return entity;
    } else if (data.success) {
      const errorCell: Excel.ErrorCellValue = {
        type: Excel.CellValueType.error,
        basicValue: `#Empty response`,
      };
      return errorCell;
    } else {
      const errorCell: Excel.ErrorCellValue = {
        type: Excel.CellValueType.error,
        basicValue: `#${data.error}`,
      };
      return errorCell;
    }
  } catch (error) {
    console.error(`Error in ANSWER function:`, error);
    const errorCell: Excel.ErrorCellValue = {
      type: Excel.CellValueType.error,
      basicValue: `#${error.message}`,
    };
    return errorCell;
  }
}

async function gptCall(ticker: string, query: string, answer: string) {
  const apiKey = getFromLocalStorage("GPTKey");
  if (!apiKey) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "API key not found in settings");
  }

  const client = new AzureOpenAI({
    baseURL: "https://apoidea-chatgpt-api.openai.azure.com/openai/deployments/gpt4o-mini-deployment/",
    apiVersion: "2024-05-01-preview",
    apiKey,
    deployment: "gpt4o-mini-deployment",
    dangerouslyAllowBrowser: true,
  });

  const response = await client.chat.completions.create({
    model: "gpt4o-mini-deployment",
    messages: [
      {
        role: "system",
        content: `You are a helpful assistant providing factual information about companies. Output in JSON: {"answer": number, "reason": string}`,
      },
      {
        role: "user",
        content: `Stock Code: ${ticker}\n Question:${query} Answer: ${answer}`,
      },
    ],
    max_tokens: 50,
    temperature: 0.9,
  });
  // if (response.status === 401) {
  //   throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "API Key not valid");
  // }

  const json = JSON.parse(response.choices[0].message.content.trim());

  return Number(json["answer"]);
}

/**
 * Answers questions about a company based on its stock symbol in a short format.
 * @customfunction ARBOR_NUMBER
 * @param {string} ticker The stock symbol of the company
 * @param {string} query The question to ask about the company
 * @returns {Promise<Excel.EntityCellValue | Excel.ErrorCellValue>} The answer to the query
 */
export async function answerNumber(
  ticker: string,
  query: string
): Promise<Excel.EntityCellValue | Excel.ErrorCellValue> {
  try {
    const companyData = await companyProfileLoader.load(ticker);
    if (!companyData) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.notAvailable, `Record Not found: ${ticker}`);
    }
    const apiToken = getFromLocalStorage("ArborApiToken");
    const provider = await getFromWorkbookSettings("arborModelProvider");
    let queryPrompt = `Stock Code: ${ticker}\nQuestion: ${query}\nAnswer the question concisely with an elliptical response (aka short answer) based on the given company and stock code. Output in JSON: {"answer": number, "reason": string}`;

    Object.keys(companyData).forEach((key) => {
      queryPrompt = queryPrompt.replace(new RegExp(`\\{\\{${key}\\}\\}`, "g"), companyData[key]);
    });

    const requestBody = JSON.stringify({
      query: queryPrompt,
      provider: provider || "openai",
    });

    const arborAnswerApi =
      getFromLocalStorage("ArborAnswerApi") || "https://stockchat-intelligence-service.arbor.news/api/v1/answer";
    const response = await arborAxiosLimiter
      .post(arborAnswerApi, requestBody, {
        headers: {
          "Content-Type": "application/json",
          "X-API-KEY": apiToken,
          "X-API-TOKEN": apiToken,
        },
      })
      .catch((error) => {
        console.error(`Error in ANSWER function:`, error);
        throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, error.message);
      });

    if (response.status >= 400) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const data = response.data;

    if (data.success && data.result?.answer) {
      const numberAnswer = await gptCall(ticker, query, data.result.answer);
      if (!numberAnswer) {
        throw new CustomFunctions.Error(CustomFunctions.ErrorCode.notAvailable, "N/A");
      }
      const entity: Excel.EntityCellValue = {
        type: Excel.CellValueType.entity,
        text: `${numberAnswer || 0}`,
        properties: {
          type: {
            type: Excel.CellValueType.string,
            basicValue: "number",
          },
          success: {
            type: Excel.CellValueType.boolean,
            basicValue: true,
          },
          ticker: {
            type: Excel.CellValueType.string,
            basicValue: ticker,
          },
          query: {
            type: Excel.CellValueType.string,
            basicValue: query || "",
          },
          answer: {
            type: Excel.CellValueType.double,
            basicValue: numberAnswer || 0,
          },
          reason: {
            type: Excel.CellValueType.string,
            basicValue: data.result.answer || "",
          },
          answerAt: {
            type: Excel.CellValueType.string,
            basicValue: new Date(data.result.answerAt).toISOString() || "",
          },
          answerId: {
            type: Excel.CellValueType.string,
            basicValue: data.result.id || "",
          },
          queryAt: {
            type: Excel.CellValueType.string,
            basicValue: new Date(data.result.queryAt).toISOString() || "",
          },
          references: {
            type: Excel.CellValueType.string,
            basicValue: data.result.references.map((ref) => ref.url).join("\n"),
          },
        },
        basicType: Excel.RangeValueType.error,
        basicValue: "#VALUE!",
      };
      return entity;
    } else if (data.success) {
      const errorCell: Excel.ErrorCellValue = {
        type: Excel.CellValueType.error,
        basicValue: `#Empty response`,
      };
      return errorCell;
    } else {
      const errorCell: Excel.ErrorCellValue = {
        type: Excel.CellValueType.error,
        basicValue: `#${data.error}`,
      };
      return errorCell;
    }
  } catch (error) {
    console.error(`Error in ANSWER function:`, error);
    const errorCell: Excel.ErrorCellValue = {
      type: Excel.CellValueType.error,
      basicValue: `#${error.message}`,
    };
    return errorCell;
  }
}

/**
 * Answers factual questions about a company based on its stock symbol.
 * @customfunction ARBOR_FACTUAL
 * @param {string} ticker The stock symbol of the company
 * @param {string} query The factual question to ask about the company
 * @returns {Promise<string>} The factual answer to the query
 */
export async function answerFactual(ticker: string, query: string): Promise<string> {
  try {
    const companyData = await companyProfileLoader.load(ticker);
    if (!companyData) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.notAvailable, `Record Not found: ${ticker}`);
    }
    const apiKey = getFromLocalStorage("GPTKey");
    if (!apiKey) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "API key not found in settings");
    }

    const client = new AzureOpenAI({
      baseURL: "https://apoidea-chatgpt-api.openai.azure.com/openai/deployments/gpt4o-mini-deployment/",
      apiVersion: "2024-05-01-preview",
      apiKey,
      deployment: "gpt4o-mini-deployment",
      dangerouslyAllowBrowser: true,
    });

    const response = await client.chat.completions.create({
      model: "gpt4o-mini-deployment",
      messages: [
        {
          role: "system",
          content: `You are a helpful assistant providing factual information about companies. Answer the query concisely with an elliptical response (aka short answer) based on the given company and stock code. Output in JSON: {"answer": string}`,
        },
        {
          role: "user",
          content: `Company: ${companyData.companyName}\nStock Code: ${ticker}\n Question:${query}`,
        },
      ],
      max_tokens: 50,
      temperature: 0.9,
    });
    // if (response.status === 401) {
    //   throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "API Key not valid");
    // }

    const json = JSON.parse(response.choices[0].message.content.trim());

    return json["answer"] || "";
  } catch (error) {
    if (error.status === 401) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "API Key not valid");
    }
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.notAvailable, "Unknown error in ANSWER_FACTUAL function");
  }
}

/**
 * Returns company information as an entity
 * @customfunction COMPANY_INFO
 * @param {string} ticker The stock ticker symbol
 * @returns {Promise<Excel.EntityCellValue>} The company data as an entity
 */
export async function companyInfo(ticker: string): Promise<Excel.EntityCellValue> {
  try {
    const data = await companyProfileLoader.load(ticker);
    if (!data) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.notAvailable, `Record Not found: ${ticker}`);
    }

    const entity: Excel.EntityCellValue = {
      type: Excel.CellValueType.entity,
      text: `${ticker} Info`,
      properties: {
        type: {
          type: Excel.CellValueType.string,
          basicValue: "info",
        },
        ticker: {
          type: Excel.CellValueType.string,
          basicValue: ticker,
        },
        ...Object.fromEntries(
          COMPANY_PROFILE_KEYS.map((key) => [key, { type: Excel.CellValueType.string, basicValue: data[key] || "" }])
        ),
      },
      basicType: Excel.RangeValueType.error,
      basicValue: "#VALUE!",
    };

    return entity;
  } catch (error) {
    console.error(`Error in ARBOR_DATA function:`, error);
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Error in ARBOR_DATA function");
  }
}

/**
 * Returns the value of the specified balance sheet key for the given stock symbol.
 * @customfunction BALANCE_SHEET
 * @param {string} ticker The stock symbol to fetch data for
 * @param {number} year The year to fetch data for
 * @param {number} [quarter] The quarter to fetch data for, default 0 for full year, 1 for Q1, 2 for Q2, 3 for Q3, 4 for Q4
 * @returns {Promise<Excel.EntityCellValue>} The value of the specified balance sheet key
 */
export async function balanceSheet(ticker: string, year: number, quarter: number = 0): Promise<Excel.EntityCellValue> {
  if (!ticker) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Stock symbol is required");
  }
  if (!year) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Year is required");
  }

  const response = await balanceSheetLoader.load({ symbol: ticker, year, quarter });
  if (!response) {
    const quarterText = quarter ? `Q${quarter}` : "Full Year";
    throw new CustomFunctions.Error(
      CustomFunctions.ErrorCode.notAvailable,
      `Record Not found: ${ticker} ${year} ${quarterText}`
    );
  }
  return {
    type: Excel.CellValueType.entity,
    text: `${ticker} Balance Sheet ${year} ${quarter ? `Q${quarter}` : "Full Year"}`,
    properties: {
      type: {
        type: Excel.CellValueType.string,
        basicValue: "balanceSheet",
      },
      ticker: {
        type: Excel.CellValueType.string,
        basicValue: response.symbol,
      },
      ...Object.fromEntries(
        STATEMENT_KEYS.map((key) => [key, { type: Excel.CellValueType.string, basicValue: response[key] || "" }])
      ),
      ...Object.fromEntries(
        BALANCE_SHEET_KEYS.map((key) => [key, { type: Excel.CellValueType.double, basicValue: response[key] || 0 }])
      ),
    },
  };
}

